:root {
  --white: #fff;
  --black: #141414;
  --dark-green: #1e2827;
  --light-green: #c6cdd1;
  --gray: rgba(20, 20, 20, .5);
  --light-gray: rgba(20, 20, 20, .4);
  --border: rgba(255, 255, 255, .1);
}

.header {
  background: var(--black);
  padding: 36px 0;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  width: 123px;
  height: 19px;
}

.header-navigation {
  align-items: center;
  column-gap: 80px;
  display: flex;
}

.header-navigation-list {
  align-items: center;
  gap: 40px;
  display: flex;
}

.header-navigation-list li {
  align-items: center;
  display: flex;
}

.header-navigation-link {
  color: var(--white);
  text-transform: uppercase;
  font-family: Epilogue;
  font-size: 9px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  transition: transform .25s ease-in-out;
}

.header-navigation-link:hover {
  transform: scale(1.2);
}

.header-button {
  color: var(--white);
  text-align: center;
  text-transform: uppercase;
  background-color: var(--dark-green);
  border: none;
  border-radius: 8px;
  padding: 18px 36px;
  font-family: Epilogue;
  font-size: 9px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  transition: background-color .25s ease-in-out, color .25s ease-in-out;
}

.header-button:hover {
  background-color: var(--white);
  color: var(--dark-green);
}

.hamburger-container {
  display: none;
}

@media screen and (max-width: 1176px) {
  .header {
    padding: 20px 0;
  }
}

@media screen and (max-width: 767px) {
  .container {
    width: 100%;
  }

  .header {
    padding: 36px 0;
  }

  .header-navigation-list, .header-button {
    display: none;
  }

  .hamburger-container {
    justify-content: flex-end;
    display: flex;
  }
}

.hamburger {
  width: 100%;
  height: 100%;
  background: var(--dark-green);
  z-index: 999;
  padding: 36px 20px;
  transition-property: transform, opacity;
  transition-duration: .25s, .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1), cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(0);
}

.hamburger.is-hidden {
  transform: translateX(100%);
}

.hamburger-close-icon {
  fill: var(--white);
}

.menu-burger-container {
  align-content: center;
  justify-content: space-between;
  display: flex;
}

.hamburger-close {
  color: var(--white);
}

.hamburger-menu-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hamburger_link {
  color: var(--white);
  padding: 7px 0;
  display: block;
}

